@import '../../../assets/_vars.scss';

.containerHistory{
    height: 100%;
    display: flex;
    flex-direction: column;
    width: 100%;
    font-size: $fontSmallSize;

    .firstRow{
        display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;

    p{
        color: $PrimaryTextColor
    }
    .hideIcon{
        color: $SecondaryTextColor;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 0.2rem;
    }
    }

    table {
        border-collapse: collapse;
        width: 100%;
        font-size: $fontSmallSize;
      }

      th, td {
        padding: 8px;
        text-align: left;
        border-bottom: 1px solid #ddd;
      }
      .tdBody{
        color: $SecondaryTextColor;
        input{
          border: none;
          background-color: transparent;
        }
      }
      .tdBody:last-child{
        color: $PrimaryTextColor
      }
      .lastRow{
        color: $PrimaryTextColor;
        width: 90%;
        display: flex;
        justify-content: flex-end;
      }
}

.light{

    .firstRow{
    p{
        color: $PrimaryTextColor
    }
    .hideIcon{
        color: $SecondaryTextColor;
    }
    }

      .tdBody{
        color: $SecondaryTextColor;

      }
      .tdBody:last-child{
        color: $PrimaryTextColor
      }
      .lastRow{
        color: $PrimaryTextColor;
      }

}

.dark{

  .firstRow{
  p{
      color: $PrimaryTextDark
  }
  .hideIcon{
      color: $White;
  }
  }

    .tdBody{
      color: $White;
      input{
        color: $White
      }
    }
    .tdBody:last-child{
      color: $PrimaryTextDark
    }
    .lastRow{
      color: $PrimaryTextDark;
    }

}

:hover .hideIcon, .lastRow p{
  cursor: pointer;
}